<template lang="pug">
  v-layout(row wrap).result-programs
    template(v-if="chartData")
      v-flex(md6)
        h2.text-uppercase.text-center.mb-3 Диаграмма ваших интересов
        ve-pie(:data="chartData" :settings="chartSettings" :legend-visible="false")
    v-flex(md5)
      h2.text-uppercase.mb-3 Результат
      p По результатам приключения Вы вернули счастье в семью короля,
        |  а искусственный интеллект познакомился с Вами поближе,
        |  что дает ему возможность предположить, какие кружки и секции могут Вас заинтересовать.
      p Итак, он (интеллект) считает, что:
        |  Вам обязательно нужно попробовать для себя кружки/секции,
        |  направленные на развитие интереса - <...>. Именно он наиболее выразился в ходе ответов на вопросы.
        |  Также Вам стоит обратить внимание на образовательные программы, созданные для детей со следующими интересами: <...>
      p  Надеемся, что Вы были искренни в своих ответах,
        |  потому что в ближайший год Ваши рекомендации,
        |  предоставляемые навигатором дополнительного образования будут ориентироваться на указанные выше интересы.
      v-btn.ml-0(@click="$emit('newGame')" color="primary" outline) Начать заново
</template>

<script>
import VePie from 'v-charts/lib/pie.common';

import { getPrevailingClasses } from '@/utils/result_processing';

export default {
  name: 'RecommendedLessons',
  components: {
    VePie,
  },
  props: {
    result: {
      type: Object,
      required: false,
      default: () => {},
    },
    gender: {
      type: Number,
      required: false,
      default: 0,
    },
  },
  data() {
    this.chartSettings = {
      offsetY: 200,
      radius: 140,
      dataType: () => '',
    };
    return {
      prevailingClassesList: null,
      prevailingClassesObject: null,
      chartData: null,
    };
  },
  created() {
    this.getResult();
  },
  methods: {
    initChart() {
      const columns = ['class', 'percent'];
      const rows = this.prevailingClassesList.map(item => ({ class: item.class_ru, percent: item.value }));
      this.chartData = { columns, rows };
    },
    async getResult() {
      // deepclone
      const result = JSON.parse(JSON.stringify(this.result));

      this.prevailingClassesList = await getPrevailingClasses(result);

      const prevailingClassesObject = {};
      this.prevailingClassesList.forEach((item) => {
        prevailingClassesObject[item.class] = item.value;
      });

      this.prevailingClassesObject = prevailingClassesObject;

      this.initChart();
    },
  },
};
</script>
