<template lang="pug">

v-container(fill-height)
  v-layout(row justify-center align-center)
    v-flex(md12 lg10)
      template(v-if="!isGameEnded")
        h2.mb-2 Добро пожаловать в модуль определения образовательных потребностей в дополнительном образовании.

        template(v-if="certificate")
          p Ваш сертификат:
            strong  {{ certificate.username }}

        p  Мы вам зададим несколько вопросов, после чего вам будут предложены подходящие по вашим интересам кружки,
          |  секции или творческие объединения.
        p  Старайтесь отвечать на вопросы максимально точно, тогда результат будет более верным.
        .full-width.mt-3
          template(v-if="isGameStarted")
            v-progress-linear(:value="stagesPercent"  height="25").progress
              .progress-description Пройдено {{ Math.ceil(stagesPercent) }}%
            v-btn.ml-0(@click="newGame" color="primary" outline) Начать заново
          template(v-if="!isGameEnded")
            v-btn.ml-0(:to="{ name: 'poll' }" color="primary" depressed) {{ isGameStarted ? 'Продолжить' : 'Начать' }} тест

      template(v-else)
        recommended-lessons.mt-3(
          :result="resultClasses"
          :gender="user.gender"
          @newGame="newGame"
        )
</template>

<script>
import { mapGetters } from 'vuex';

import TheFooter from '@/components/TheFooter';
import RecommendedLessons from '@/components/RecommendedLessons';

import { getGameLength } from '@/game';


const gameLength = getGameLength();

export default {
  name: 'StartPage',
  components: {
    TheFooter,
    RecommendedLessons,
  },
  computed: {
    ...mapGetters({
      currentSection: 'getCurrentSection',
      resultClasses: 'getResultClasses',
      user: 'getUser',
      certificate: 'getCertificate',
    }),
    isGameStarted() {
      return this.currentSection && this.currentSection > 1;
    },
    isGameEnded() {
      return this.currentSection && this.currentSection === gameLength;
    },
    stagesPercent() {
      return (100 / gameLength) * this.currentSection;
    },
  },
  methods: {
    newGame() {
      // Временный метод для тестов
      this.$store.dispatch('RESET_TEST');
      this.$router.push({ name: 'poll' });
    },
  },
};
</script>

<style scoped lang="scss">
.progress {
  border-radius: 4px;
}
.progress-description {
  color: white;
  text-align: center;
  font-weight: bold;
  height: 100%;
  font-family: Roboto, sans-serif;
  font-weight: bold;
  text-transform: uppercase;
  display: flex;
  justify-content: center;
  align-items: center;
}
</style>
