import { slides } from '../data/slides';

const getSumResultList = (resultList) => {
  const sum = resultList.reduce(
    (acc, curr) => (acc + curr.value),
    0,
  );
  return sum;
};

export const getPrevailingClasses = (result) => {
  // убираем временно не задействованный класс
  const resultList = Object.values(result).filter(key => key.class !== 'reliability');
  // сортировака по убыванию баллов
  const sortedResultList = resultList.sort((a, b) => b.value - a.value);

  const sum = getSumResultList(sortedResultList);

  return sortedResultList.reduce(
    (acc, curr) => {
      const percentsSum = getSumResultList(acc);
      if (percentsSum >= 75) return acc;

      curr.value = curr.value / sum * 100;
      acc.push(curr);
      return acc;
    },
    [],
  );
};

/**
 *  Фильтрация результатов и возврат только нужных классов
 *  Преобладающий, Дополняющий, Слабый
 * @param result - результат тестирования
 * @param gender - пол пользователя (м == 1/ж == -1)
 * @returns {{prevailing: *, complementary: *, weak: *}} - { Преобладающий
 *   класс, Дополняющий класс, Слабый класс, }
 */
export const filterResult = (result, gender) => {
  // убираю значение "достоверность"
  result = Object.values(result).filter(key => key.class !== 'reliability');

  // сортировака по убыванию баллов
  result = Object.values(result).sort((a, b) => b.value - a.value);

  let prevailingVal = result[0].value;
  let complementaryVal = result[1].value;
  let weakVal = result[result.length - 1].value;

  let filteredResult = Object.values(result).filter(key => [prevailingVal, complementaryVal, weakVal].includes(key.value));

  // TODO сделать порядок
  if (filteredResult[0].value === filteredResult[1].value || filteredResult.length > 3) {
    const genderSlide = slides.filter(i => i.id === 'gender')[0];
    const genderClasses = genderSlide.question.answers[gender === 1 ? 0 : 1].classes;
    genderClasses.forEach((item) => {
      filteredResult.forEach((res) => {
        if (res.class === item.class) {
          res.value -= item.ball;
        }
      });
    });

    filteredResult = Object.values(filteredResult).sort((a, b) => b.value - a.value);
    prevailingVal = filteredResult[0].value;
    complementaryVal = filteredResult[1].value;
    weakVal = filteredResult[filteredResult.length - 1].value;
    filteredResult = Object.values(result).filter(key => [prevailingVal, complementaryVal, weakVal].includes(key.value));
    filteredResult = Object.values(filteredResult).sort((a, b) => b.value - a.value);
  }


  return {
    prevailing: filteredResult[0],
    complementary: filteredResult[1],
    weak: filteredResult.slice(2),
  };
};

/**
 * Вернет классы для программ на основе интересов
 * если совпадение по высоким - выбираем "по интересам" только "собственные"
 * пересечения (например, знаки-знаки, техника-техника и т.д.)
 * @param prevailing - Преобладающий
 * @param complementary - Дополняющий
 * @param keys - все классы
 * @returns {[*, *]} - [Программы: Преобладающий - Преобладающий, Программы:
 *   Преобладающий - Дополняющий]
 */
export const returnByInterest = ({ prevailing, complementary }, keys) => {
  const first = keys[prevailing.class][prevailing.class].name;

  let second;
  if (prevailing.value === complementary.value) {
    second = keys[complementary.class][complementary.class].name;
  } else {
    second = keys[prevailing.class][complementary.class].name;
  }

  return [first, second];
};

/**
 * Вернет классы для программ отстающие для подтягивания
 * если совпадение по низким - берем все на "подтянуть".
 * @param prevailing - Преобладающий
 * @param complementary - Преобладающий
 * @param weak - Слабый класс (для подтягивания)
 * @param keys - все классы
 * @returns {[*, *]} - [Программы: Преобладающий - Слабый, Программы:
 *   Дополняющий - Слабый]
 */
export const returnForPullingUp = ({ prevailing, complementary, weak }, keys) => {
  const weakPrograms = [];
  weak.forEach((item) => {
    weakPrograms.push(keys[prevailing.class][item.class].name);
    weakPrograms.push(keys[complementary.class][item.class].name);
  });
  return weakPrograms;
};

export default { getPrevailingClasses };
